import { Injectable } from '@nestjs/common';

import { Personnel, UpdatePersonnel } from '../entity';
import { PersonnelId } from '../value';

@Injectable()
export abstract class PersonnelRepository {
  abstract create(personnel: Personnel): Promise<Personnel>;
  abstract update(updatePersonnel: UpdatePersonnel): Promise<Personnel>;
  abstract getById(personnelId: PersonnelId): Promise<Personnel>;
}
